import React from 'react';
import ProgressBar from '../components/ProgressBar';

const Skills = () => (
  <div id="skills" className="py-5 container">
    <h1>
      Skills &amp; languages
    </h1>
    <p className="text-uppercase candara" style={{ fontSize: 24 }}>
      What I bring to the table
    </p>
    <div className="border-bottom p-3 pb-5">
      <div className="row">
        <ProgressBar title="React Native" value="98" />
        <ProgressBar title="PCB Soldering" value="93" />
      </div>

      <div className="row">
        <ProgressBar title="Bootstrap v4.5" value="90" />
        <ProgressBar title="React" value="88" />
      </div>

      <div className="row">
        <ProgressBar title="HTML" value="85" />
        <ProgressBar title="JavaScript" value="85" />
      </div>

      <div className="row">
        <ProgressBar title="Redux" value="85" />
        <ProgressBar title="Analog Design" value="78" />
      </div>

      <div className="row">
        <ProgressBar title="Raspberry Pi" value="75" />
        <ProgressBar title="C++" value="70" />
      </div>

      <div className="row">
        <ProgressBar title="MATLAB" value="70" />
        <ProgressBar title="GitHub" value="70" />
      </div>

      <div className="row">
        <ProgressBar title="Linux Systems" value="60" />
        <ProgressBar title="PhotoShop" value="55" />
      </div>
    </div>

    <div>
      <div className="col animate-box p-3">
        <div className="progress-wrap">
          <div className="d-flex justify-content-between">
            <span>Spanish</span>
            <span>95%</span>
          </div>
          <div className="progress mt-2" style={{ height: 4 }}>
            <div
              className="progress-bar color-4"
              aria-valuenow={95}
              aria-valuemin="0"
              aria-valuemax="100"
              style={{ width: `${95}%` }}
            />
          </div>
        </div>
      </div>

      <div className="col animate-box p-3">
        <div className="progress-wrap">
          <div className="d-flex justify-content-between">
            <span>English</span>
            <span>90%</span>
          </div>
          <div className="progress mt-2" style={{ height: 4 }}>
            <div
              className="progress-bar color-4"
              aria-valuenow={90}
              aria-valuemin="0"
              aria-valuemax="100"
              style={{ width: `${90}%` }}
            />
          </div>
        </div>
      </div>

      <div className="col animate-box p-3">
        <div className="progress-wrap">
          <div className="d-flex justify-content-between">
            <span>Chinese</span>
            <span>80%</span>
          </div>
          <div className="progress mt-2" style={{ height: 4 }}>
            <div
              className="progress-bar color-4"
              aria-valuenow={80}
              aria-valuemin="0"
              aria-valuemax="100"
              style={{ width: `${80}%` }}
            />
          </div>
        </div>
      </div>

      <div className="col animate-box p-3">
        <div className="progress-wrap">
          <div className="d-flex justify-content-between">
            <span>French</span>
            <span>45%</span>
          </div>
          <div className="progress mt-2" style={{ height: 4 }}>
            <div
              className="progress-bar color-4"
              aria-valuenow={45}
              aria-valuemin="0"
              aria-valuemax="100"
              style={{ width: `${40}%` }}
            />
          </div>
        </div>
      </div>
    </div>
  </div>
);

export default Skills;
