import React from 'react';

const Projects = () => (
  <div id="projects" className="container-fluid">
    <h1 className="py-5 container">
      Engineering Projects
    </h1>

    <div className="mx-5 my-3">
      <div className="row justify-content-around">
        <div className="col-12 col-md-6 d-flex justify-content-center align-items-center">
          <img className="img-fluid" src="../assets/images/farmbeats.png" alt="FarmBeats" />
        </div>
        <div className="col-12 col-md-6 d-flex justify-content-center px-2 px-lg-4" style={{ fontSize: 18 }}>
          <div className="col-9 d-flex flex-column justify-content-center pb-4">
            <strong className="text-left w-100 mb-3" style={{ fontSize: 20 }}>Microsoft FarmBeats</strong>
            <p className="text-justify">
              As part of our project in industry during the 3rd year, I
              was working with Microsoft in their project about Microsoft
              FarmBeats. Our work consisted in developing educational
              material to teach undergraduate students, just as
              urselves, about IoT and Microsoft Azure Services in
              agricultural technology.
            </p>

            <p className="text-justify">
              The team came up with 4 different real life scenarios,
              so that the material starts with the basics, and become
              progressively harder. The student will learn how to use
              a raspberry pi, create an app using powerapps and lear
              to use pre-trained machine learning modules among others
              skills. For more information, please refer to the
              <a href="https://github.com/microsoft/FarmBeats-UniversityCommunity/tree/master/community-members/Imperial%20College%20London/Scenario-Learning-Modules"> official repo.</a>
            </p>
          </div>
        </div>
      </div>

      <div className="row mb-5">
        <div className="col-12 col-md-6 d-flex justify-content-center align-items-center order-md-2">
          <img className="img-fluid" src="../assets/images/motor.png" alt="Brushless Motor" />
        </div>
        <div className="col-12 col-md-6 d-flex justify-content-center px-2 px-lg-4" style={{ fontSize: 18 }}>
          <div className="col-9 d-flex flex-column justify-content-center py-4">
            <strong className="text-left w-100 mb-3" style={{ fontSize: 20 }}>Brushless Motor Control</strong>
            <p className="text-justify">
              During this project, we use real time programming in C to
              execute different commands from the user. This included
              motor control (velocity and position), printing messages
              in the terminal, playing a melody and perform a Bitcoin
              mining (encryption task).
            </p>

            <p className="text-justify">
              All this was managed using different threads for each
              commands, and mutex to protect the common variables between
              threads. The most challenging part of this project was the
              motor controller which used an Interrupt Service Routine
              (ISR). The motor had to spin for x rotations at a maximum
              speed of y, where x and y are input by the user. To do this,
              we used a Proportional Integral Derivative (PID) controller
              for the rotation control and a Proportional Derivative (PD)
              for the speed.
            </p>
          </div>
        </div>
      </div>

      <div className="row py-4">
        <div className="col-12 col-md-6 d-flex justify-content-center align-items-center">
          <img className="img-fluid" src="../assets/images/eatRaspi.png" alt="Eat RasPi" />
        </div>
        <div className="col-12 col-md-6 d-flex justify-content-center px-2 px-lg-4" style={{ fontSize: 18 }}>
          <div className="col-9 d-flex flex-column justify-content-center py-4">
            <strong className="text-left w-100 mb-3" style={{ fontSize: 20 }}>Save Red Light</strong>
            <p className="text-justify">
              About 939 people were killed due to cars running red lights
              in 2017. That is an increase of a 30% with respect to 2009.
              To reduce this number, our team proposed an innovative IoT idea.
            </p>
            <p className="text-justify">
              Our product used a load sensor as a trigger to detect if a car
              has passed through it while the traffic light is red. In  a real
              life application, this sensor would be placed underground, and
              trigger when a certain threshold is passed. We also added a
              raspberry pi camera to leave evidence that the user has infringed
              the law. A mobile application was also created to notify the driver
              about all the infrictions by sending an image, time and place over
              MQTT. This communication is protected by using SSL and a quality
              of service (QoS) of 2. For more information, please refer to this
              webpage:
              <a href="https://jasonshengyu0222.wixsite.com/eatraspberrypi"> https://jasonshengyu0222.wixsite.com/eatraspberrypi</a>.
            </p>
          </div>
        </div>
      </div>

      <div className="row py-4">
        <div className="col-12 col-md-6 d-flex justify-content-center align-items-center order-md-2">
          <img className="img-fluid" src="../assets/images/musicap.jpg" alt="MusicCap" />
        </div>
        <div className="col-12 col-md-6 d-flex justify-content-center px-2 px-lg-4" style={{ fontSize: 18 }}>
          <div className="col-9 d-flex flex-column justify-content-center py-4">
            <strong className="text-left w-100 mb-3" style={{ fontSize: 20 }}>Musicap</strong>
            <p className="text-justify">
              We believe music can have a great impact in a person’s life.
              However, not everyone can play an instrument. As a group, we came
              up with an idea to make a musical instrument for the disabled,
              focusing on amputees, as most instruments require the use of our
              hands.
            </p>
            <p className="text-justify">
              Our project used an accelerometer and gyroscope to play a note
              according to the movement of the head. To make it more accurate,
              we implemented an additional air flow sensor. This way, the note
              will only be played if the user blows into the pipe.
            </p>

            <p className="text-justify">
              Besides, we also wrote a python program to give the user real-time
              feedback in a digital piano. The arduino used was connected via
              luetooth to the computer, and showed the  notes pressed on the
              piano accordingly.
            </p>
          </div>
        </div>
      </div>

      <div className="row py-4">
        <div className="col-12 col-md-6 d-flex justify-content-center align-items-center">
          <img className="img-fluid" src="../assets/images/rover.jpg" alt="EE Rover" />
        </div>
        <div className="col-12 col-md-6 d-flex justify-content-center px-2 px-lg-4" style={{ fontSize: 18 }}>
          <div className="col-9 d-flex flex-column justify-content-center py-4">
            <strong className="text-left w-100 mb-3" style={{ fontSize: 20 }}>EE Rover</strong>
            <p className="text-justify">
              As part of our 1st year group project, we made a robot to detect
              different type of signals. These included radio waves, audio waves,
              magnetic field and infrared waves. In addition, some of the radio
              waves signals were AM modulated.
            </p>

            <p className="text-justify">
              All the amplifier and demodulation circuits were designed from
              cratch. Furthermore, a bluetooth module (BLE) was used to control
              the robot wirelessly via the adafruit mobile application. Although,
              control of the motor could have been via wifi, this was not viable
              due to the latency.
            </p>

            <p className="text-justify">
              The whole robot was controlled with a simple Arduino Uno
              microcontroller. This project served as a motivation for me to get
              involved into the electronics world.
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export default Projects;
