import React from 'react';

const About = () => (
  <div style={{ backgroundColor: '#60606018' }}>
    <div id="about" className="py-5 container">
      <h1>
        About
      </h1>
      <p className="text-uppercase candara" style={{ fontSize: 24 }}>
        My background
      </p>
      <p className="text-justify pt-3" style={{ fontSize: 22 }}>
        Pasionate about circuits and design, understanding how things
        around us work, getting to work with people from different cultures.
        That is ME!
      </p>

      <p className="text-justify" style={{ fontSize: 22 }}>
        I am always open to new opportunities, new experiences that will
        make me grow, not only professionally, but also personally. At the
        moment, I just have experience in Mobile App Development and teaching
        as UT at university and IB in high school. Nonetheless, I would like
        to extend my professional skills towards hardware design in biomedical
        applications.
      </p>
    </div>
  </div>
);

export default About;
